.comentarios-container {
    text-align: center;
    font-family: 'Arial', sans-serif;
    overflow: hidden;
    position: relative;
    height: 100vh;
    background-color: #f0f0f0;
}

.comentarios-container h1 {
    padding-top: 20px;
    font-size: 2.5em;
    color: #333;
}

.chuva-de-comentarios {
    position: relative;
    height: 100%;
}

.comentario {
    position: absolute;
    white-space: nowrap; /* Impede que o texto quebre em múltiplas linhas */
    font-size: 1.5em;
    color: #444;
    opacity: 0; /* Inicia todos os comentários como invisíveis */
    animation: chuva 8s linear infinite;
}
@keyframes chuva {
    0% {
        transform: translateY(-100vh) translateX(0); /* Inicia bem acima da tela */
        opacity: 0;
    }
    1% {
        opacity: 0; /* Mantém invisível no início para evitar mostrar no topo */
    }
    5% {
        opacity: 1; /* Torna visível rapidamente após entrar na tela */
    }
    95% {
        opacity: 1; /* Mantém visível durante a descida */
    }
    100% {
        transform: translateY(100vh); /* Vai até o fim da tela */
        opacity: 0; /* Desaparece no final da tela */
    }
}


/* Animações distintas para variar a posição de partida */
.comentario-0 { animation-delay: 0s; left: calc(5%); }
.comentario-1 { animation-delay: 1.5s; left: calc(10%); }
.comentario-2 { animation-delay: 3s; left: calc(15%); }
