.comentario-8 { animation-delay: 12s; left: calc(15%); }
.comentario-9 { animation-delay: 13.5s; left: calc(50%); }
.comentario-10 { animation-delay: 15s; left: calc(55%); }
.comentario-11 { animation-delay: 16.5s; left: calc(60%); }
.comentario-12 { animation-delay: 18s; left: calc(65%); }
.comentario-13 { animation-delay: 19.5s; left: calc(70%); }
.comentario-14 { animation-delay: 21s; left: calc(65%); }
.comentario-15 { animation-delay: 22.5s; left: calc(15%); }
.comentario-16 { animation-delay: 24s; left: calc(25%); }
.comentario-17 { animation-delay: 25.5s; left: calc(10%); }
.comentario-18 { animation-delay: 27s; left: calc(85%); }
.comentario-19 { animation-delay: 28.5s; left: calc(50%); }


/* CSS ajustado para responsividade em dispositivos móveis */
@media (max-width: 768px) {
    .comentarios-container h1 {
        font-size: 2em; /* Reduz o tamanho da fonte para o título em dispositivos menores */
    }

    .comentario {
        font-size: 1em; /* Reduz o tamanho da fonte para os comentários */
    }

    /* Ajusta as posições iniciais para menor espaçamento entre eles */
