/* SubscriptionButton.css */

.subscription-button {
    display: inline-block;
    position: relative;
    cursor: pointer;
    overflow: hidden;
}

.subscription-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.5); /* Cor do raio com transparência */
    z-index: -1;
    transform: scaleX(0); /* Inicialmente sem largura */
    transform-origin: left;
    transition: transform 0.3s ease-out;
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
}

.subscription-button:hover::before {
    transform: scaleX(1); /* Amplia o raio ao passar o mouse */
}


.subscription-button p {
    position: relative;
    z-index: 1;
    display: inline-block;
    padding: 10px 20px; /* Aumenta o padding para melhor visibilidade */
    background: linear-gradient(120deg, #8e24aa, #ffeb3b);
    color: #fff;
    font-size: 12px;
    font-weight: bold;
    border-radius: 8px;
    text-align: center;
    animation: moveButton 3s infinite alternate; /* Adiciona a animação de movimento */
    transition: background 0.1s ease-out, transform 0.1s ease-out;
}

@keyframes moveButton {
    0% {
        transform: translateX(0);
    }
    50% {
        transform: translateY(3px); /* Desloca para a direita */
    }   
}

.subscription-button:hover p {
    background: linear-gradient(120deg, #673ab7, #ffc107);
    animation-play-state: paused; /* Pausa a animação ao passar o mouse */
}
@media (min-width: 430px) {
    .subscription-button p {
        padding: 8px 16px; /* Reduz o padding para dispositivos menores */
        font-size: 100%; /* Reduz o tamanho da fonte para dispositivos menores */
    }
}