.modal-square {
  background-color: #ffffff;
  width: 90%; /* Adjust for responsive width */
  max-width: 800px; /* Maximum width constraint */
  height: auto; /* Auto height for flexibility */
  border-radius: 20px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  overflow-y: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 20px auto; /* Centered with margin auto */
}

.interests-container {
  max-width: 100%;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
  position: relative; /* Add this line */
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

.title {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin-left: 0%;
}

.back-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 10px 15px;
  border-radius: 25px;
  background-color: transparent;
  color: #000000;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: color 0.2s, transform 0.2s;
  border: none;
  margin: 20px 0;
  max-width: fit-content;
}

.back-button:hover {
  color: #32eef5fb;
  transform: translateY(-2px);
}

.subtitle {
  font-size: 16px;
  color: #555;
  margin-bottom: 20px;
  margin-top: -2%;
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  padding-bottom: 60px; /* Adjusted to give space for the sticky button */
}

.card {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: calc(50% - 10px);
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  transition: transform 0.2s, box-shadow 0.2s;
  text-align: left;
  cursor: pointer;
  overflow: hidden;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.card-content p {
  margin: 0;
  font-size: 14px;
  font-weight: 500;
}

.checked {
  border: 2px solid #007bff;
}

.checked::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(90, 155, 224, 0.2);
  pointer-events: none;
}

.card-icon {
  font-size: 24px;
  margin-bottom: 10px;
  color: #007bff;
}

.savebutton {
  position: fixed; 
  bottom: 20px; 
  left: 50%; 
  transform: translateX(-50%); 
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 40px;
  border-radius: 25px;
  background-color: #89CFF0; /* Baby blue for save button */
  color: white;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s, transform 0.2s;
  border: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: 80%; 
  max-width: 600px; 
  z-index: 1000;
}

.savebutton:hover {
  background-color: #0056b3;
  transform: translateX(-50%) translateY(-10px); /* Makes the button "jump" up */
  transition: transform 0.2s ease, background-color 0.2s ease; /* Smooth transition */
}

.savebutton:active {
  transform: translateX(-50%) translateY(-5px); /* Slightly lowers the button when clicked */
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .savebutton {
    width: 90%; 
    font-size: 18px; 
  }
}

@media (max-width: 480px) {
  .savebutton {
    width: 95%; 
    font-size: 16px; 
  }
}
