/* Planos.css */
.planos-container {
    display: flex; /* Usando Flexbox para centralizar conteúdo */
    flex-direction: column; /* Coloca o conteúdo em uma coluna */
    justify-content: center; /* Centraliza verticalmente */
    align-items: center; /* Centraliza horizontalmente */
    text-align: center; /* Centraliza o texto */
    font-family: 'Arial', sans-serif; /* Fonte padrão */
    padding: 20px; /* Espaçamento ao redor do conteúdo */
    width: 100vw; /* Ocupa 100% da largura da tela */
    height: 100vh; /* Ocupa 100% da altura da tela */
    margin: 0; /* Remove margens externas */
    background-color: #f7f7f7; /* Cor de fundo suave */
    box-sizing: border-box; /* Inclui padding e borda no tamanho total */
}

/* Estilos para o Título */
.planos-container h1 {
    font-size: 4em; /* Tamanho grande para o título */
    color: #333; /* Cor escura para o texto */
    margin-bottom: 0.5em; /* Espaço abaixo do título */
    max-width: 90%; /* Garante que o título não ultrapasse a tela */
}

/* Estilos para o Texto */
.planos-container p {
    font-size: 1.8em; /* Tamanho grande para o texto */
    color: #666; /* Cor cinza para o texto */
    margin-bottom: 1.5em; /* Espaço abaixo do texto */
    max-width: 80%; /* Garante que o texto não ultrapasse a tela */
}

/* Estilos para o Botão */
.button-azul-bebe {
    background-color: #00c3ff; /* Azul bebê */
    color: white; /* Cor do texto */
    border: none; /* Remove bordas */
    padding: 20px 50px; /* Tamanho grande do botão */
    cursor: pointer; /* Cursor de ponteiro ao passar o mouse */
    border-radius: 8px; /* Bordas arredondadas */
    font-size: 20px; /* Texto grande */
    font-weight: bold; /* Texto em negrito */
    transition: transform 0.3s ease, background-color 0.3s ease; /* Animações de transição */
}

.button-azul-bebe:hover {
    transform: scale(1.1); /* Aumenta o botão ao passar o mouse */
    background-color: #009cda; /* Azul mais escuro ao passar o mouse */
}

/* Responsividade para dispositivos móveis */
@media (max-width: 768px) {
    .planos-container h1 {
        font-size: 3em; /* Tamanho ajustado para o título */
    }

    .planos-container p {
        font-size: 1.5em; /* Tamanho ajustado para o texto */
        max-width: 90%; /* Garante que o texto não ultrapasse a tela */
    }

    .button-azul-bebe {
        padding: 25px 90px; /* Ajuste de padding para o botão */
        font-size: 18px; /* Tamanho ajustado do texto do botão */
    }
}

@media (max-width: 480px) {
    .planos-container h1 {
        font-size: 2.5em; /* Tamanho ajustado para o título em smartphones */
    }

    .planos-container p {
        font-size: 1.2em; /* Tamanho ajustado para o texto */
        max-width: 100%; /* Garante que o texto não ultrapasse a tela */
    }

    .button-azul-bebe {
        padding: 20px 60px; /* Ajuste de padding para o botão */
        font-size: 16px; /* Tamanho ajustado do texto do botão */
    }
}
