.ranking {
  font-family: Arial, sans-serif;
  background-color: #f4f4f9;
  padding: 40px 0;
  margin-top: 5%;
}

.ranking-container {
  max-width: 700px;
  background-color: #fff;
  padding: 30px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  text-align: center;
  margin: 0 auto;
  position: relative;
}

.ranking-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  position: relative;
  flex-wrap: wrap;
}

.ranking-title {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  font-size: 24px;
}

.ranking-subtitle {
  font-size: 30px;
  margin: 20px 0 10px;
  color: #333;
}

.prizes-images {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 20px 0;
  margin-left: -10%;
}

.prize-image {
  max-width: 150px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-left:10%;
}


.ranking-logo {
  margin-right: 10px;
  font-size: 24px;
  vertical-align: middle;
}

.ranking-back-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 15px;
  border-radius: 25px;
  background-color: transparent;
  color: #000000;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: color 0.2s, transform 0.2s;
  border: none;
  margin: 10px 0;
}

.ranking-back-button:hover {
  color: #32eef5fb;
  transform: translateY(-2px);
}

.ranking-table {
  width: 100%;
  margin-top: 20px;
  border-collapse: collapse;
  overflow-x: auto;
}

.ranking-table th,
.ranking-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: center;
}

.ranking-gold {
  color: gold;
}

.ranking-silver {
  color: silver;
}

.ranking-bronze {
  color: #cd7f32;
}

.ranking-learn-more-button {
  padding: 12px 30px;
  border-radius: 25px;
  background-color: #89cff0;
  color: white;
  cursor: pointer;
  transition: background-color 0.2s, transform 0.2s;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border: none;
  margin: 10px;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  text-decoration: none;
}

.ranking-learn-more-button:hover {
  background-color: #0575ec;
  transform: translateY(-2px);
}

.ranking-learn-more-button:active {
  transform: translateY(1px);
}

.ranking-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.ranking-modal-content {
  background-color: #fff;
  padding: 30px;
  border-radius: 10px;
  max-width: 600px;
  width: 90%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow-y: auto;
  max-height: 90vh;
}

.ranking-close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
  border: none;
  background: none;
}

.ranking-modal-content h2 {
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

.ranking-modal-content p {
  margin-bottom: 10px;
  line-height: 1.5;
  color: #555;
}

.ranking-modal-content ul {
  list-style-type: none;
  padding: 0;
}

.ranking-modal-content ul li {
  margin-bottom: 10px;
  color: #555;
}

.ranking-modal-content a {
  color: #0575ec;
  text-decoration: none;
}

.ranking-modal-content a:hover {
  text-decoration: underline;
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  .ranking-header {
    flex-direction: row;
    justify-content: space-between;
  }

  .ranking-title {
    justify-content: flex-start;
    margin-left: 0px;
  }

  .ranking-back-button {
    justify-self: flex-end;
  }

  .ranking-table th,
  .ranking-table td {
    font-size: 14px;
  }

  .ranking-modal-content {
    width: 95%;
  }
}

@media (max-width: 480px) {
  .ranking-title {
    font-size: 20px;
    margin-left: 0px;
  }

  .ranking-back-button,
  .ranking-learn-more-button {
    font-size: 14px;
  }
}
