/* Estilos gerais para a navbar (aplicável a desktop e mobile) */
.desktop-navbar {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    background: #fff; /* Fundo sólido padrão para mobile */
    transition: transform 0.3s ease, opacity 0.3s ease; /* Transição suave para ocultar a navbar */
}

/* Ajustes para desktop */
@media (min-width: 769px) {
    .desktop-navbar {
        background: transparent; /* Fundo transparente apenas para desktop */
    }
}

.desktop-logo {
    font-size: 28px;
    font-weight: bold;
    color: #00b4fc; /* Cor do logo ajustada para melhor visibilidade com fundo transparente */
    text-decoration: none;
    padding: 0;
}

.desktop-logo-img {
    max-height: 60px; /* Ajusta a altura máxima do logotipo para mantê-lo dentro da navbar */
    height: auto;
    width: auto;
}

.desktop-nav-menu {
    display: flex;
    align-items: center;
    transition: transform 0.3s ease, opacity 0.3s ease; /* Adiciona transição */
}

.hide-nav {
    transform: translateY(-100%); /* Move o menu para cima, escondendo-o */
    opacity: 0; /* Faz o menu ficar transparente */
    visibility: hidden; /* Torna o menu completamente invisível após a animação */
}

.desktop-nav-link {
    position: relative;
    display: inline-block;
    overflow: hidden;
    margin: 0 20px;
    color: transparent; /* Texto inicialmente transparente */
    text-decoration: none;
    font-weight: 566;
    cursor: pointer;
    transition: color 0.3s ease; /* Transição suave de cor */
}

.desktop-nav-link::before,
.desktop-nav-link::after {
    content: attr(data-text);
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    transition: top 0.3s ease;
    color: #333; /* Cor do texto */
}

.desktop-nav-link::before {
    top: 100%;
    color: #00b4fc; /* Cor ao passar o mouse */
}

.desktop-nav-link::after {
    top: 0;
}

.desktop-nav-link:hover::before {
    top: 0;
}

.desktop-nav-link:hover::after {
    top: -100%;
}

.desktop-right-menu {
    display: flex;
    align-items: center;
    gap: 5px;
}

.desktop-language-selector {
    cursor: pointer;
}

.desktop-language-selector img {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    border: 2px solid #00b4fc;
    transition: transform 0.3s ease, border-color 0.3s ease;
    object-fit: cover;
}

.desktop-language-selector:hover img {
    transform: scale(1.1);
    border-color: #009ec3;
}

.desktop-action-button {
    padding: 8px 15px;
    background-color: #00b4fc;
    color: white;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    font-weight: bold;
    margin-left: 5px;
    transition: background-color 0.3s ease; /* Adiciona transição suave */
}

.desktop-action-button:hover {
    background-color: #009ec3;
}
