/* UniqueAnimation.css */
.unique-animation-container {
    width: 100vw; /* Largura da tela */
    height: 100vh; /* Altura da tela */
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f0f8ff; /* Fundo branco gelo */
    overflow: hidden; /* Impede qualquer rolagem */
    position: relative;
    transition: background-color 1.5s ease-in-out;
    margin: 0; /* Remover margem */
    padding: 0; /* Remover padding */
    box-sizing: border-box; /* Para incluir padding e border na largura e altura */
}

.shapes {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%; /* Ocupa toda a largura do contêiner */
    height: 100%; /* Ocupa toda a altura do contêiner */
    overflow: hidden;
    z-index: 1;
}

.shape {
    position: absolute;
    top: -50px; /* Mantém a forma fora da tela inicialmente */
    width: 60px;
    height: 60px;
    opacity: 0.5;
    animation: fall 6s infinite ease-in-out;
}

.circle {
    background-color: #004080;
    border-radius: 50%;
    left: 20%;
}

.triangle {
    width: 0;
    height: 0;
    border-left: 30px solid transparent;
    border-right: 30px solid transparent;
    border-bottom: 60px solid #004080;
    left: 50%;
}

.square {
    background-color: #004080;
    left: 80%;
}

.hexagon {
    width: 52px;
    height: 60px;
    background-color: #004080;
    clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
    left: 30%;
}

.centered-text {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    z-index: 2; /* Acima das formas */
    text-align: center;
    padding: 0; /* Remover padding */
}

.word {
    font-size: 5vw; /* Tamanho base das palavras */
    font-weight: bold;
    color: #000000; /* Letras pretas com sombra */
    margin: 10px; /* Margem entre palavras */
    opacity: 1;
    transform: scale(1);
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease-out, color 0.2s ease-in-out;
}

.word:hover {
    color: #0375e7; /* Muda para azul ao passar o mouse */
}

@keyframes fall {
    0% {
        transform: translateY(-100px) rotate(0deg);
        opacity: 0.5;
    }
    50% {
        opacity: 1;
    }
    100% {
        transform: translateY(120vh) rotate(360deg);
        opacity: 0;
    }
}

@media (max-width: 768px) {
    .word {
        font-size: 6vw; /* Ajuste de tamanho para tablets */
    }

    .shape {
        width: 50px; /* Diminuir tamanho das formas em tablets */
        height: 50px;
    }
}

@media (max-width: 480px) {
    .word {
        font-size: 20vw; /* Aumenta o tamanho do texto em dispositivos móveis */
    }

    .shape {
        width: 40px; /* Diminuir tamanho das formas em dispositivos móveis */
        height: 40px;
    }
}
