.skeleton-loader {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
  }
  
  .skeleton-row {
    display: flex;
    gap: 20px;
  }
  
  .skeleton-cell {
    background-color: #e0e0e0;
    border-radius: 4px;
    width: 100px;
    height: 20px;
    animation: shimmer 1.5s infinite linear;
  }
  
  .skeleton-position {
    width: 50px;
  }
  
  .skeleton-name {
    flex: 1;
    width: 150px;
  }
  
  .skeleton-conversations {
    width: 100px;
  }
  
  @keyframes shimmer {
    0% {
      background-position: -200px 0;
    }
    100% {
      background-position: 200px 0;
    }
  }
  