/* Conversação Container */
.conversacao-container {
    text-align: center;
    font-family: 'Arial', sans-serif;
    max-width: 800px;
    margin: auto;
    padding-top: 1em; /* Espaço adicional no topo */
    padding-left: 10px; /* Adiciona padding lateral para evitar textos colados nas bordas */
    padding-right: 10px;
}


a{
    text-decoration: none;
}
/* Estilos para o Título */
h1 {
    font-size: 3em; /* Aumenta o tamanho */
    color: #333;
    margin: 1em 0; /* Ajusta o espaçamento acima e abaixo */
}

/* Estilos para o Parágrafo */
p {
    color: #666;
    font-size: 1.5em; /* Aumenta o tamanho para o texto */
    margin: 1.5em 0; /* Espaço acima e abaixo */
}

/* Estilos para o Texto em Destaque */
.porque {
    color: #000000;
    font-size: 2.5em; /* Aumenta o tamanho */
    margin: 1em 0;
}

/* Estilos para o Texto Menor */
.texto-menor {
    font-size: 1em; /* Aumenta o tamanho do texto menor */
    color: #666;
    margin: 2em 0; /* Ajuste do espaço */
}

/* Estilos para Imagens */
.about-image {
    max-width: 100%;
    height: auto;
    margin: 20px 0;
    transition: opacity 0.5s ease-in-out; /* Transição suave de 0.5 segundos */
}

/* Estilos para Botões */
.button-azul {
    background-color: #00c3ff; /* Azul bebê mais forte */
    color: white;
    border: none;
    padding: 30px 90px; /* Tamanho grande do botão */
    cursor: pointer;
    border-radius: 60px;
    font-size: 25px; /* Texto maior */
    font-weight: 900;
    transition: transform 0.3s ease, background-color 0.3s ease;
    display: block;
    margin: 15px auto;
}

.button-azul:hover {
    transform: scale(1.2); /* Aumenta um pouco mais */
    background-color: #009cda; /* Azul mais forte ao passar o mouse */
}

/* Estilos para Categorias */
.categorias span {
    display: inline-block;
    margin: 5px;
    background-color: #f0f0f0;
    padding: 10px 15px; /* Aumenta o padding */
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-size: 1.2em; /* Aumenta o tamanho dos rótulos */
}

.categorias span:hover {
    background-color: #e0e0e0;
}

/* Estilos para Chamadas para Ação */
.call-to-action {
    margin-top: 20px;
    font-size: 18px; /* Aumenta o tamanho */
    font-weight: bold;
}

.call-to-action p {
    margin: 10px 0;
}

/* Responsividade para dispositivos móveis */
@media (max-width: 768px) {
    .conversacao-container {
        padding: 1em; /* Reduz o padding em dispositivos móveis */
    }

    h1 {
        font-size: 3em; /* Tamanho ajustado para o título */
    }

    p, .porque, .texto-menor {
        font-size: 1em; /* Tamanho ajustado para o texto */
    }

    .button-azul {
        padding: 15px 40px; /* Ajuste de padding para o botão */
        font-size: 20px; /* Tamanho ajustado do texto do botão */
    }

    .categorias span {
        font-size: 1em; /* Tamanho ajustado para as categorias */
    }

    .about-image {
        margin: 15px 0; /* Menor margem para imagens */
    }
}

@media (max-width: 480px) {
    .conversacao-container {
        padding: 1em; /* Reduz o padding em dispositivos móveis */
    }

    h1 {
        font-size: 3em; /* Tamanho ajustado para o título em smartphones */
    }
    .porque{
        font-size: 2.5em; /* Ajuste do tamanho do texto */

    }
    p , .texto-menor {
        font-size: 1em; /* Ajuste do tamanho do texto */
    }

    .button-azul {
        padding: 25px 60px; /* Ajuste de padding para o botão */
        font-size: 18px; /* Tamanho ajustado do texto do botão */
    }

    .call-to-action {
        font-size: 16px; /* Tamanho ajustado para a chamada para ação */
    }

    .categorias span {
        font-size: 0.9em; /* Tamanho ajustado para as categorias */
    }

    .about-image {
        margin: 10px 0; /* Menor margem para imagens */
    }
}


