/* NotFound.css */

.not-found-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #e0f7fa; /* Azul claro */
  color: #212121; /* Preto */
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  overflow: hidden;
  padding: 20px;
}

.not-found-content {
  animation: fadeIn 1.5s ease-out;
  max-width: 700px;
  padding: 30px;
  background-color: rgba(255, 255, 255, 0.9); /* Fundo branco translúcido */
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
}

.horse-animation {
  margin-bottom: 20px;
}

.horse-image {
  width: 150px;
  animation: float 3s ease-in-out infinite;
  border-radius: 26%;
}

.not-found-title {
  font-size: 6rem;
  margin: 0;
  color: #0277bd; /* Azul escuro */
  animation: pulse 2s infinite;
}

.not-found-message {
  font-size: 1.5rem;
  margin: 10px 0;
  animation: slideIn 1s ease-out;
  color: #455a64;
}

.not-found-description {
  font-size: 1rem;
  margin: 20px 0;
  animation: slideIn 1.5s ease-out;
  color: #607d8b;
}

.not-found-link {
  display: inline-block;
  margin-top: 20px;
  padding: 12px 25px;
  color: #ffffff;
  background-color: #0277bd; /* Azul escuro */
  text-decoration: none;
  font-weight: bold;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s, transform 0.3s;
}

.not-found-link:hover {
  background-color: #005b9f; /* Azul ainda mais escuro */
  color: #ffffff;
  transform: translateY(-3px);
}

/* Responsividade */
@media (max-width: 768px) {
  .not-found-title {
    font-size: 4rem;
  }

  .not-found-message {
    font-size: 1.2rem;
  }

  .not-found-description {
    font-size: 0.9rem;
  }

  .horse-image {
    width: 120px;
  }
}

@media (max-width: 480px) {
  .not-found-title {
    font-size: 3rem;
  }

  .not-found-message {
    font-size: 1rem;
  }

  .not-found-description {
    font-size: 0.8rem;
  }

  .horse-image {
    width: 100px;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes slideIn {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
