/* Estilos para a página de planos do Fluable */
.fluable-planos-page {
  padding: 130px 20px;
  background-color: #f7f8fa;
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.fluable-title {
  text-align: center;
  color: #4ecdff;
  font-size: 46px;
  margin-bottom: 60px;
}

.fluable-planos-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  width: 100%;
}

.fluable-plano-card {
  perspective: 1000px;
  width: 350px;
  height: 450px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fluable-plano-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.fluable-plano-card.flipped .fluable-plano-card-inner {
  transform: rotateY(180deg);
}

.fluable-plano-card-front,
.fluable-plano-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.fluable-plano-card-front {
  z-index: 2;
  transform: rotateY(0deg);
  background-color: #ffffff; /* Frontal com fundo branco */
}

.fluable-plano-card-back {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px); /* Efeito de vidro fosco */
  transform: rotateY(180deg);
  justify-content: center;
}

.fluable-plano-card:hover .fluable-plano-card-front {
  transform: translateY(-10px);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.15);
}

.fluable-plano-card h3 {
  font-size: 39px;
  margin-bottom: 20px;
  color: #333;
  font-weight: 800;
  margin-top: -30px;
}

.fluable-plano-card .subtitle {
  font-size: 12px;
  color: #777;
  margin-bottom: 10px;
}

.fluable-plano-card .price {
  font-size: 23px;
  color: #4ecdff;
  font-weight: 300;
  margin-bottom: 20px;
}

.fluable-features {
  list-style-type: none;
  padding: 0;
  margin-bottom: 26px;
  font-weight: 600;
  text-align: left;
}

.fluable-feature-item {
  font-size: 14px;
  color: #555;
  margin-bottom: 5px;
}

.fluable-proceed-button {
  display: block;
  width: 80%;
  padding: 10px;
  background-color: #4ecdff;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
  text-align: center;
  transition: background-color 0.3s ease;
}

.fluable-proceed-button:hover {
  background-color: #0096db;
}

.fluable-detail-link {
  color: #004080;
  text-decoration: none;
  font-size: 16px;
  transition: transform 0.3s ease;
  cursor: pointer;
}

.fluable-detail-link:hover {
  transform: translateY(-3px);
  color: #002060;
  text-decoration: underline;
}

.fluable-detail-item {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: left;
  gap: 8px;
}

.fluable-plano-card-back .fluable-features {
  text-align: center;
  margin-top: 20px;
}

/* Responsividade */
@media (max-width: 768px) {
  .fluable-planos-container {
    flex-direction: column;
    align-items: center;
  }

  .fluable-plano-card {
    width: 80%;
    margin-bottom: 20px;
  }

  .fluable-title {
    font-size: 36px;
    margin-bottom: 40px;
  }

  .fluable-plano-card h3 {
    font-size: 30px;
    margin-top: -20px;
  }

  .fluable-plano-card .price,
  .fluable-plano-card .subtitle {
    font-size: 18px;
  }

  .fluable-proceed-button {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .fluable-plano-card {
    width: 100%;
    height: 380px;
  }

  .fluable-plano-card h3 {
    font-size: 26px;
    margin-top: -15px;
  }

  .fluable-plano-card .subtitle,
  .fluable-plano-card .price,
  .fluable-feature-item {
    font-size: 16px;
  }

  .fluable-proceed-button {
    font-size: 12px;
  }
}
