/* ContactPage.css */

.contact-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #9ae2ff 0%, #fecfef 100%); /* Cores vibrantes */
  font-family: Arial, sans-serif;
  padding: 20px; /* Adiciona padding para evitar elementos grudados nas bordas */
  box-sizing: border-box; /* Garante que padding seja considerado no tamanho total */
  overflow: hidden; /* Remove qualquer scroll desnecessário */
}

.company-logo {
  max-width: 100%; /* Garante que a logo não exceda a largura da tela */
  width: 300px; /* Tamanho inicial da logo */
  height: auto; /* Mantém a proporção da imagem */
  margin-bottom: 30px; /* Ajuste do espaço inferior */
  transition: transform 0.3s ease;
}

.company-logo:hover {
  transform: scale(1.1) rotate(5deg);
}

.contact-text {
  font-size: 24px;
  color: #ffffff;
  background-color: #333333;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  transition: transform 0.3s ease, background-color 0.3s ease, color 0.3s ease;
  text-align: center;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  display: inline-block;
  position: relative;
  max-width: 100%; /* Garante que o texto não exceda a largura da tela */
  box-sizing: border-box; /* Inclui padding no cálculo da largura */
}

.contact-text:hover {
  transform: scale(1.05);
  background-color: #00e676;
  color: #000000;
}

.contact-text::before,
.contact-text::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #f793fb 0%, #ec6577e5 100%);
  transition: transform 0.6s ease;
  z-index: -1;
  opacity: 0.6;
}

.contact-text::before {
  top: -10px;
  left: -10px;
  transform: translate(-100%, -100%);
}

.contact-text::after {
  bottom: -10px;
  right: -10px;
  transform: translate(100%, 100%);
}

.contact-text:hover::before {
  transform: translate(0, 0);
}

.contact-text:hover::after {
  transform: translate(0, 0);
}

/* Responsividade */
@media (max-width: 768px) {
  .company-logo {
    width: 180px; /* Ajuste do tamanho da logo para tablets */
    margin-bottom: 20px;
  }

  .contact-text {
    font-size: 20px;
    padding: 8px 16px;
  }
}

@media (max-width: 480px) {
  .company-logo {
    width: 100%; /* Ajuste do tamanho da logo para smartphones */
    margin-bottom: 15px;
  }

  .contact-text {
    font-size: 18px;
    padding: 6px 12px;
  }
}
