body {
    font-family: Arial, sans-serif;
    background: #f4f4f9;
    /* padding: 20px; */
    color: #333;
}
.container {
    background: white;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
    max-width: 400px;
    margin: auto;
}
.section {
    border-bottom: 2px solid #eee; /* Ajuste para uma borda mais sutil */
    padding: 10px 0;
}
.section:last-child {
    border-bottom: none;
}
.item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 16px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out; /* Unificado com outros transitions */
}
.item:hover {
    background-color: #e8e8e8;
}
.item:hover .icon, .item:hover .icon-chevron {
    color: #007BFF; /* Azul mais escuro para ícones */
}
.icon, .icon-chevron {
    display: flex;
    align-items: center;
    transition: color 0.3s ease-in-out;
}
.icon {
    font-size: 24px;
    color: #0899dd; /* Cor ajustada para azul bebê */
    margin-right: 10px;
}
.icon-chevron {
    font-size: 16px;
    color: #ccc; /* Cor inicial para o ícone chevron */
}
.text {
    flex: 1;
    padding-left: 10px;
    font-size: 16px; /* Mantido como único para evitar duplicação */
}
.right.active {
    color: green;
    font-weight: bold;
}

.right.blocked {
    color: red;
    font-weight: bold;
}

.right.pending {
    color: orange;
    font-weight: bold;
}

.button {
    display: inline-block;
    background-color: #2a91ff;
    color: #ffffff;
    padding: 10px 20px;
    border-radius: 20px;
    text-decoration: none;
    font-size: 16px;
    margin: 5px;
    cursor: pointer;
    border: none;
    transition: background-color 0.3s;
}
.button:hover {
    background-color: #0575ec;
}
.h1-central{
    font-size: 139%;
}

