/* Design do contêiner futurista */
.content-container {
  background: #f0f0f0; /* Fundo geral mais claro */
  padding: 50px 0; /* Espaço ao redor */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden; /* Oculta o overflow para os elementos animados */
  position: relative; /* Para efeitos adicionais */
  min-height: 50vh; /* Garante que o contêiner ocupe pelo menos metade da tela */
  width: 90%; /* Limita a largura */
  max-width: 1000px; /* Tamanho máximo para grandes telas */
  border: 3px solid #aaa; /* Borda para um efeito de pergaminho */
  border-radius: 30px; /* Borda arredondada */
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2), -10px -10px 20px rgba(255, 255, 255, 0.8); /* Sombra futurista */
  background: linear-gradient(145deg, #e2e2e2, #ffffff); /* Efeito de fundo futurista */
  animation: containerGlow 5s infinite alternate; /* Animação de brilho */
  box-sizing: border-box; /* Inclui padding na largura */
  text-align: center; /* Centraliza o texto */
  margin: 30px 0; /* Espaço entre as seções */
  margin-left: 16%;
  margin-top: 10%; /* Adiciona margem superior para compensar a altura da navbar */
  align-items: center; /* Centraliza o conteúdo */
}


.highlight-link {
  color: rgb(223, 189, 0);
  text-decoration: none; /* Remove o sublinhado padrão */
  font-weight: bold; /* Negrito para dar destaque */
  cursor: pointer; /* Indicador de link clicável */
}

.highlight-link-pro {
  color: rgb(31, 91, 253);
  text-decoration: none;
  font-weight: bold;
  cursor: pointer;
}

.highlight-link-basico {
  color: lightblue;
  text-decoration: none;
  font-weight: bold;
  cursor: pointer;
}

.highlight-link-sem-limites {
  color: red;
  text-decoration: none;
  font-weight: bold;
  cursor: pointer;
}

.highlight-link:hover,
.highlight-link-pro:hover,
.highlight-link-basico:hover,
.highlight-link-sem-limites:hover,
.highlight-link-ranking:hover {
  text-decoration: underline;
}

/* Efeito de brilho animado para o contêiner */
@keyframes containerGlow {
  0% {
    box-shadow: 0 0 10px rgba(0, 174, 255, 0.4), 0 0 20px rgba(0, 174, 255, 0.4), 0 0 30px rgba(0, 174, 255, 0.4);
  }
  100% {
    box-shadow: 0 0 20px rgba(0, 255, 255, 0.8), 0 0 30px rgba(0, 255, 255, 0.8), 0 0 40px rgba(0, 255, 255, 0.8);
  }
}

.content-title {
  font-size: 4rem; /* Aumenta o tamanho da fonte */
  margin-bottom: 20px;
  color: #333;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1); /* Sombra no texto */
  animation: titleColorChange 3s infinite; /* Animação de mudança de cor */
}

/* Animação de mudança de cor e salto para os títulos */
@keyframes titleColorChange {
  0% {
    color: #333;
    transform: translateY(0);
  }
  50% {
    color: #00aaff; /* Azul */
    transform: translateY(-10px); /* Efeito de salto */
  }
  100% {
    color: #333;
    transform: translateY(0);
  }
}

.content-subtitle {
  font-size: 3rem; /* Aumenta o tamanho da fonte */
  margin-bottom: 30px;
  color: #444;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1); /* Sombra no texto */
  animation: subtitleLetterJump 6s infinite; /* Animação de salto e cor das letras */
}

/* Animação de salto e cor para letras individuais */
@keyframes subtitleLetterJump {
  0% {
    color: #444;
    transform: scale(1) translateY(0);
  }
  10%, 30% {
    color: #00aaff; /* Azul */
    transform: scale(1.1) translateY(-5px); /* Salto */
  }
  20%, 40% {
    color: #444; /* Preto */
    transform: scale(1) translateY(0);
  }
  50% {
    color: #00aaff; /* Azul */
    transform: scale(1.1) translateY(-5px); /* Salto */
  }
  60%, 80% {
    color: #444;
    transform: scale(1) translateY(0);
  }
  100% {
    color: #00aaff; /* Azul */
    transform: scale(1.1) translateY(-5px); /* Salto final */
  }
}

.content-text {
  font-size: 1.8rem; /* Aumenta o tamanho da fonte */
  line-height: 2.2rem; /* Melhora a legibilidade */
  max-width: 800px; /* Limita a largura do texto */
  color: #555;
  margin: 20px 0;
  padding: 15px; /* Adiciona espaçamento interno */
  background: rgba(255, 255, 255, 0.7); /* Fundo leve para contraste */
  border-radius: 15px; /* Borda arredondada */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /* Sombra leve */
}

.content-image,
.content-carousel img {
  width: 100%;
  max-width: 350px; /* Tamanho máximo ajustado para melhor visualização */
  height: auto; /* Mantém a proporção da imagem */
  border-radius: 20px; /* Borda arredondada */
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1), 0 -8px 15px rgba(255, 255, 255, 0.3); /* Sombra dupla para efeito de pergaminho */
  margin: 20px 0;
  transition: opacity 1.5s ease-in-out, transform 1.5s ease-in-out; /* Animação suavizada */
  opacity: 1; /* Visível por padrão */
  transform: translateX(0); /* No centro por padrão */
}
