.plano-detalhes-container {
  padding: 50px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f7f8fa;
  min-height: 100vh;
}

.plano-detalhes-container h2 {
  font-size: 28px;
  color: #6b47dc;
  margin-bottom: 40px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.features-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  max-width: 1000px;
}

.plano-feature-card {
  width: 100%;
  max-width: 300px;
  padding: 20px;
  border-radius: 15px;
  background: linear-gradient(135deg, #f0f8ff, #d7e9f7);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.plano-feature-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
}

.plano-feature-card p {
  font-size: 18px;
  color: #333;
  margin: 10px 0;
}

.plano-feature-card::before {
  content: "✔️";
  font-size: 24px;
  color: #4caf50;
  display: block;
  margin-bottom: 10px;
}

.plano-feature-card::after {
  content: '';
  display: block;
  width: 60px;
  height: 4px;
  background-color: #6b47dc;
  margin: 15px auto 0;
  border-radius: 2px;
}

/* Responsividade */
@media (max-width: 768px) {
  .plano-detalhes-container h2 {
    font-size: 24px;
  }

  .features-container {
    flex-direction: column;
    align-items: center;
  }

  .plano-feature-card {
    width: 80%; /* Ajuste para dispositivos menores */
  }
}

@media (max-width: 480px) {
  .plano-detalhes-container h2 {
    font-size: 20px;
  }

  .plano-feature-card {
    max-width: 100%; /* Ocupa a largura total em dispositivos muito pequenos */
    font-size: 16px; /* Texto menor para legibilidade em telas pequenas */
  }

  .plano-feature-card::before {
    font-size: 20px; /* Ícone um pouco maior em dispositivos pequenos */
  }

  .plano-feature-card::after {
    width: 50px; /* Barra decorativa mais estreita em telas pequenas */
    height: 3px; /* Altura ajustada */
  }
}
