.upcoming-container {
    position: relative;
    width: 100%;
    height: 100vh;
    background-image: url('../assets/img/background_abstract_purple.jpeg');
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: white;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
}

.upcoming-container .icon {
    font-size: 10rem; /* Ajuste inicial para telas grandes */
    animation: blink 9s infinite alternate, colorChange 6s infinite alternate;
}

@keyframes blink {
    0%, 100% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
}

@keyframes colorChange {
    0%, 100% { color: purple; }
    50% { color: black; }
}

.animated-text {
    font-size: 7rem; /* Tamanho inicial para telas grandes */
    font-weight: bold;
    animation: typing 6s steps(40, end) 3s infinite normal, blink-caret 3s step-end infinite;
    white-space: nowrap;
    overflow: hidden;
    border-right: 6px solid white;
}

@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: white }
}

/* Media Queries para adaptar o layout em dispositivos menores */
@media (max-width: 768px) {
    .upcoming-container .icon {
        font-size: 6rem; /* Tamanho reduzido para telas de tablets */
    }

    .animated-text {
        font-size: 4rem; /* Tamanho do texto ajustado para tablets */
    }
}

@media (max-width: 480px) {
    .upcoming-container .icon {
        font-size: 6rem; /* Tamanho reduzido para telas de smartphones */
    }

    .animated-text {
        font-size: 1.6rem; /* Tamanho do texto ajustado para smartphones */
        padding: 0 20px; /* Adiciona padding para evitar que o texto toque as bordas */
    }
}
