/* BlankFooter.css */
.blank-footer {
    width: 100%;
    padding: 10px 20px;
    background: linear-gradient(to top, #ffffff, #9ae2ff 50%); /* Transição de branco para azul bebê */
    color: #333; /* Texto em cinza escuro para contraste */
    text-align: center;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1); /* Sombra sutil na parte superior */
    font-size: 14px;
    border-top: 1px solid #eee; /* Linha sutil na parte superior para definição */
    position: relative; /* Permite sobreposição de elementos */
    z-index: 2; /* Garante que o footer fique sobre outros elementos quando rolar */
}

/* Adiciona gradiente ao fim da página de contato para suavizar a transição para o footer */
.contact-page {
    position: relative; /* Permite sobreposição de gradientes */
}

.contact-page::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50%; /* Ocupa a metade inferior da tela */
    background: linear-gradient(to bottom, transparent, #9ae2ff 100%); /* Transparência para azul bebê */
    z-index: 1; /* Posiciona sob o conteúdo principal */
}
