/* ContactPage.css */
.contact-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(120deg, #89CFF0, #A7C7E7);
    background-size: cover;
    animation: backgroundShift 30s infinite alternate;
}

.contact-form {
    background: rgba(255, 255, 255, 0.9);
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0,0,0,0.2);
    width: 90%;
    max-width: 500px;
    text-align: center;
    position: relative;
}

.contact-form h1 {
    color: #0056b3; /* Azul escuro */
    margin-bottom: 20px;
}

.contact-form input, .contact-form textarea {
    width: 100%;
    padding: 12px;
    margin: 10px 0;
    border-radius: 5px;
    border: 2px solid transparent;
    transition: all 0.3s ease;
    outline: none;
}

.contact-form input:focus, .contact-form textarea:focus {
    border: 2px solid #5B9BD5; /* Azul claro */
    background-color: #E7F3FE;
}

.contact-form button {
    width: 100%;
    padding: 10px;
    background-color: #007BFF; /* Azul forte */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.contact-form button:hover {
    background-color: #0056b3; /* Azul escuro */
}

.social-links {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.icon {
    font-size: 2.5rem; /* Tamanho dos ícones */
    color: #007BFF;
    margin: 0 10px;
    transition: transform 0.5s ease;
}

.icon:hover {
    transform: scale(1.2); /* Efeito de ampliação ao passar o mouse */
}

.icon.whatsapp {
    color: #25D366; /* Verde WhatsApp */
}

.icon.instagram {
    color: #E1306C; /* Rosa Instagram */
}

.icon.linkedin {
    color: #0077B5; /* Azul LinkedIn */
}

@keyframes backgroundShift {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
}
