/* Reset e definições de corpo */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Arial', sans-serif;
    background-color: #f8f8f8;
    color: #333;
    margin: 0;
    padding: 0;
    width: 100%; /* Remove o uso de vw para evitar problemas de rolagem */
    min-height: 100vh;
    overflow-x: hidden; /* Evita rolagem horizontal desnecessária */
  }
  
  .homepage {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(to bottom right, #ffffff, #f0f0f0);
  }
  
  .secondary-circle {
    position: absolute;
    width: 50vw;
    height: 50vw;
    background-color: #f8f8f8;
    border-radius: 50%;
    z-index: 1; /* Garantir que fique atrás do hero-section */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1.05);
  }
  
  .hero-section {
    position: relative;
    text-align: center;
    width: 49vw;
    height: 49vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 50%;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
    z-index: 2;
  }
  
  .hero-content {
    z-index: 3;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  /* Botões com estilos combinados e transições suaves */
  .cta-button {
    background-color: #01c3ee;
    color: white;
    padding: 20px 60px;
    border-radius: 350px;
    text-decoration: none;
    font-weight: bold;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    animation: bounce 2s infinite;
    transition: transform 0.3s ease, background-color 0.3s ease;
  }
  
  .cta-button:hover {
    background-color: #009ec3;
    transform: scale(1.05);
    letter-spacing: 1px;
  }
  
  .cta-top {
    background-color: #f2f3f5;
    color: #333;
    padding: 10px 25px;
    border-radius: 100px;
    font-size: 18px;
    cursor: pointer;
    margin-bottom: 20px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    height: min-content;
    width: max-content;
    overflow: visible;
    position: relative;
    border-style: solid;
    border-width: 1px;
    border-color: #40475a;
    background: linear-gradient(180deg, #f0f3f7 0%, rgb(255, 255, 255) 100%);
    animation: moveCamera 10s ease-in-out infinite;
    transition: transform 0.3s ease, background-color 0.3s ease;
  }
  
  .cta-top:hover {
    transform: scale(1.1);
    background: linear-gradient(180deg, #d1e8f2 0%, #ffffff 100%);
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2);
  }
  
  .hero-section h1 {
    font-size: 2.5rem;
    color: #333;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 20px;
    display: flex; /* Divida as letras */
    flex-wrap: wrap; /* Permite que as letras quebrem de linha */
    justify-content: center; /* Centraliza o texto */
  }
  
  .hero-section h1 span {
    display: inline-block;
    transition: transform 0.3s ease, color 0.3s ease;
  }
  
  .hero-section h1 span:hover {
    transform: scale(1.3);
    color: #01c3ee;
  }
  
  .hero-section p {
    font-size: 26px;
    line-height: 1.6;
    color: #555;
    margin-bottom: 30px;
    max-width: 800px;
    animation: fadeInOut 4s ease-in-out infinite;
  }
  
  /* Animações e estilos gerais */
  @keyframes moveCamera {
    0% {
      transform: translate(2px, -6px);
    }
    20% {
      transform: translate(12px, -18px);
    }
    40% {
      transform: translate(-14px, 16px);
    }
    60% {
      transform: translate(18px, -12px);
    }
    80% {
      transform: translate(-16px, 14px);
    }
    100% {
      transform: translate(2px, -6px);
    }
  }
  
  @keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-10px);
    }
    60% {
      transform: translateY(-5px);
    }
  }
  
  /* Media Queries para Responsividade */
  @media (max-width: 768px) {
    .hero-section {
      width: 80vw; /* Ajusta para telas menores */
      height: 80vw;
    }
  }
  