.edit-data-container {
    max-width: 700px;
    margin: 40px auto;
    background-color: #fff;
    padding: 30px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
    border-radius: 15px;
}

.header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
}

.date-info {
    color: #777;
    margin-top: 5px;
    font-size: 14px;
}

.edit-form {
    display: flex;
    flex-direction: column;
}

.input-container {
    position: relative;
    margin-bottom: 20px;
}

.input-container input,
.input-container select {
    width: 100%;
    padding: 14px;
    padding-left: 15px;
    border: none;
    background-color: #f0f0f0;
    border-radius: 8px;
    font-size: 16px;
    outline: none;
    transition: padding-top 0.2s ease, padding-bottom 0.2s ease;
    box-sizing: border-box;
}

.input-container input::placeholder {
    color: transparent;
}

.input-container label {
    position: absolute;
    top: 50%;
    left: 15px;
    transform: translateY(-50%);
    color: #555;
    transition: 0.2s ease all;
    pointer-events: none;
    font-size: 16px;
}

.input-container input:focus ~ label,
.input-container input:not(:placeholder-shown) ~ label,
.input-container select:focus ~ label,
.input-container select:not([value=""]) ~ label {
    top: -10px;
    left: 10px;
    font-size: 12px;
    color: #555;
}

.phone-container {
    display: flex;
    align-items: center;
    gap: 15px;
}

.input-container input[readonly] {
    background-color: #e0e0e0;
    color: #777;
    cursor: not-allowed;
}

.ddi-container {
    position: relative;
    display: flex;
    align-items: center;
}

.ddi-input {
    display: flex;
    align-items: center;
    background-color: #f0f0f0;
    border-radius: 8px;
    padding-left: 10px;
    flex-grow: 1;
}

.ddi-input input {
    border: none;
    background-color: transparent;
    outline: none;
    font-size: 16px;
    padding-left: 35px; /* Space for the flag */
    width: 100%;
}

.ddi-input .react-flag-kit {
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
}

.phone-label {
    flex-grow: 1;
}

.back-button {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px 15px;
    border-radius: 25px;
    background-color: transparent;
    color: #000000;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    transition: color 0.2s, transform 0.2s;
    margin-bottom: 20px;
    border: none;
}

.back-button:hover {
    color: #32eef5fb;
    transform: translateY(-2px);
}

.save-button {
    padding: 12px 30px;
    border-radius: 25px;
    background-color: #89CFF0;
    color: white;
    cursor: pointer;
    transition: background-color 0.2s, transform 0.2s;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    border: none;
}

.save-button:hover {
    background-color: #0575ec;
    transform: translateY(-2px);
}

.save-button:active {
    transform: translateY(1px);
}
