/* WavePage.css */

.wave-container {
  position: relative;
  width: 100%;
  height: 90vh;
  background-color: #f0f8ff;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.wave-image {
  position: absolute;
  bottom: 0;
  left: 100%;
  width: 100%; /* Mantém a largura automática */
  height: 106%; /* Define a altura da imagem para preencher verticalmente */
  transform: translateX(-50%); /* Move a imagem para a esquerda apenas o suficiente para que ela se estique para o lado direito */
  transition: transform 2s ease-out;
}

.wave-text {
  position: absolute;
  left: 96%;
  color: #004080;
  font-size: 3vw; /* Ajusta o tamanho do texto para ser responsivo */
  font-weight: bold;
  white-space: nowrap;
  transition: transform 3s ease-out;
  opacity: 0;
}

.enter {
  transform: translateX(-100%);
  opacity: 1;
}

/* Ajustes para dispositivos móveis */
@media (max-width: 768px) {
  .wave-container {
    height: 60vh; /* Ajusta a altura para dispositivos móveis */
  }

  .wave-image {
    width: 120%; /* Aumenta a largura da imagem para ocupar mais espaço horizontalmente */
    height: 100%; /* Mantém a altura da imagem para preencher verticalmente */
    transform: translateX(-90%); /* Ajusta o deslocamento para a esquerda para dispositivos móveis */
  }

  .wave-text {
    font-size: 3vw; /* Aumenta o tamanho do texto para melhor legibilidade em dispositivos móveis */
    left: 100%; /* Ajusta a posição do texto para dispositivos móveis */
  }
}

@media (max-width: 480px) {
  .wave-container {
    height: 60vh; /* Ajusta ainda mais a altura para telas muito pequenas */
  }

  .wave-image {
    width: 100%; /* Estica a imagem mais horizontalmente para pequenos dispositivos */
    height: 110%; /* Mantém a altura da imagem para preencher verticalmente */
    transform: translateX(-100%); /* Ajusta o deslocamento para a esquerda para pequenas telas */
  }

  .wave-text {
    font-size: 3vw; /* Aumenta ainda mais o tamanho do texto para pequenas telas */
    left: 96%; /* Ajusta a posição do texto para pequenas telas */
  }
}
