.features-main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f5f5f5;
}

.features-content-section {
  width: 80%;
  max-width: 600px;
  background-color: #ffffff;
  padding: 20px;
  margin: 20px 0;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.features-heading {
  font-size: 2.5rem;
  color: #444;
  margin-bottom: 20px;
}

.features-description {
  font-size: 1.5rem;
  color: #555;
  margin-bottom: 20px;
}
.features-description-last{
  font-size: 1.6rem;
  color: #555;
  margin-bottom: 20px;
}

.features-access-button {
  padding: 10px 20px;
  border: none;
  background-color: #00c3ff;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: background-color 0.3s, transform 0.3s;
}

.features-access-button:hover {
  background-color: #009cda;
  transform: scale(1.1);
}

.features-footer {
  height: 100px;
}

@media (max-width: 768px) {
  .features-heading, .features-description {
    font-size: 2rem;
  }

  .features-access-button {
    font-size: 1rem;
  }
}
