.footer {
    background: linear-gradient(135deg, #9ae2ff 0%, #fecfef 100%); /* Cores vibrantes */
    padding: 40px 20px; /* Espaçamento maior */
    color: #000000; /* Cor do texto */
    text-align: center;
    position: relative;
    overflow: hidden; /* Previne rolagem lateral */
}

.footer-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.social-links {
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
    flex-wrap: wrap; /* Permite que os ícones quebrem para a linha seguinte se necessário */
}

.social-icon {
    margin: 0 15px; /* Espaço entre ícones */
    font-size: 30px; /* Tamanho maior dos ícones */
    transition: transform 0.3s ease, color 0.3s ease; /* Transições para animação */
}

.social-icon:hover {
    transform: scale(1.3); /* Aumenta o ícone ao passar o mouse */
    color: #0092e6; /* Cor ao passar o mouse */
}

.copyright {
    font-size: 16px;
    font-weight: 600;
    margin: 10px 0; /* Efeito de desfocagem sutil //filter: blur(1px); */
    transition: filter 0.6s ease; /* Transição suave para o efeito de desfocagem */
}

.footer-links {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
}

.footer-link {
    color: #ffffff; /* Cor dos links */
    text-decoration: none; /* Remove o sublinhado */
    transition: color 0.3s ease; /* Transição para a cor dos links */
    background-color: #45d2fd; /* Cor de fundo */
    padding: 10px 20px; /* Espaçamento dos links */
    border-radius: 8px; /* Bordas arredondadas */
    position: relative; /* Para animação */
    margin: 0 10px; /* Espaço entre os links */
}

.footer-link:hover {
    color: #ffffff; /* Cor ao passar o mouse */
    background-color: #0077e6; /* Altera a cor de fundo ao passar o mouse */
}

.footer-link::before,
.footer-link::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, #f793fb 0%, #ec6577e5 100%);
    transition: transform 0.6s ease;
    z-index: -1;
    opacity: 0.6;
}

.footer-link::before {
    top: -10px;
    left: -10px;
    transform: translate(-100%, -100%);
}

.footer-link::after {
    bottom: -10px;
    right: -10px;
    transform: translate(100%, 100%);
}

.footer-link:hover::before {
    transform: translate(0, 0);
}

.footer-link:hover::after {
    transform: translate(0, 0);
}

/* Responsividade */
@media (max-width: 768px) {
    .social-icon {
        font-size: 24px; /* Tamanho menor dos ícones em dispositivos móveis */
    }

    .copyright {
        font-size: 14px; /* Tamanho menor do texto de direitos autorais */
    }

    .footer-links {
        flex-direction: column; /* Coloca os links em coluna */
    }

    .footer-link {
        margin: 5px 0; /* Espaçamento entre os links */
        font-size: 12px; /* Tamanho menor do texto */
    }
}

/* Animações de arrastar para os ícones */
@keyframes icon-drag {
    0% { transform: translateY(0); }
    50% { transform: translateY(-5px); } /* Movimento menor para não distrair */
    100% { transform: translateY(0); }
}

.social-icon.is-dragging {
    opacity: 0.5; /* Reduz a opacidade durante o arrasto */
    cursor: grabbing; /* Muda o cursor para indicar movimento */
  }
  
  .footer-logo {
    max-width: 500px; /* Define um tamanho máximo */
    height: auto; /* Mantém a proporção */
  }
  

  @media (max-width: 768px) {
    .footer-logo {
        max-width: 80%; /* Reduz o tamanho máximo para evitar que saia da tela */
    }
}