/* AccessDenied.css */

.access-denied-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #e0f7fa; /* Rosa claro e0f7fa*/
  color: #212121; /* Preto */
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  overflow: hidden;
  padding: 20px;
}

.access-denied-content {
  animation: fadeIn 1.5s ease-out;
  max-width: 700px;
  padding: 30px;
  background-color: rgba(255, 255, 255, 0.9); /* Fundo branco translúcido */
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
}

.lock-animation {
  font-size: 4rem;
  animation: shake 0.5s infinite alternate;
  margin-bottom: 20px;
}

.access-denied-title {
  font-size: 6rem;
  margin: 0;
  color: #eb0808; /*eb0808 Rosa escuro d41818*/
  animation: pulse 2s infinite;
}

.access-denied-message {
  font-size: 1.5rem;
  margin: 10px 0;
  animation: slideIn 1s ease-out;
  color: #455a64;
}

.access-denied-description {
  font-size: 1rem;
  margin: 20px 0;
  animation: slideIn 1.5s ease-out;
  color: #607d8b;
}

.access-denied-link {
  display: inline-block;
  margin-top: 20px;
  padding: 12px 25px;
  color: #ffffff;
  background-color: #0abce9; /* Rosa escuro */
  text-decoration: none;
  font-weight: bold;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s, transform 0.3s;
}

.access-denied-link:hover {
  background-color: rgb(1, 47, 255); /* Rosa ainda mais escuro */
  color: #ffffff;
  transform: translateY(-3px);
}

/* Responsividade */
@media (max-width: 768px) {
  .access-denied-title {
    font-size: 4rem;
  }

  .access-denied-message {
    font-size: 1.2rem;
  }

  .access-denied-description {
    font-size: 0.9rem;
  }

  .lock-animation {
    font-size: 3rem;
  }
}

@media (max-width: 480px) {
  .access-denied-title {
    font-size: 3rem;
  }

  .access-denied-message {
    font-size: 1rem;
  }

  .access-denied-description {
    font-size: 0.8rem;
  }

  .lock-animation {
    font-size: 2.5rem;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes shake {
  from {
    transform: translateX(-5px);
  }
  to {
    transform: translateX(5px);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes slideIn {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
